.catalog-container--additional {
  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    margin: 0 110px 85px;
    justify-content: space-between;
  }
}

.catalog-container__title {
  .list-reset;
  padding: 27px 20px 41px;
  border-top: 1px solid #d9d9d9;

  @media (min-width: @tablet-width) {
    border: none;
    padding: 65px 30px 163px;
  }

  @media (min-width: @desktop-width) {
    padding: 65px 110px 163px;
  }


  &--additional {
    font-family: "Oswald", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    padding: 25px 20px 36px;

    @media (min-width: @tablet-width) {
      padding: 68px 67px 80px;
      font-size: 36px;
      line-height: 36px;
    }

    @media (min-width: @desktop-width) {
      padding: 65px 0 81px;
    }
  }
}

.catalog-container__list {
  .list-reset;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 141px;
    margin: 0 auto;
    width: 708px;
  }

  @media (min-width: @desktop-width) {
    width: 1220px;
  }
}

.catalog-container__list--additional {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    width: 708px;
    gap: 0;
  }

  @media (min-width: @desktop-width) {
    width: 896px;
    margin: 0;
  }
}
